<script setup lang="ts">
interface TabItem {
  key: string;
  label: string;
  onClick: () => void;
}

interface Props {
  tabs: TabItem[];
  activeTab: string;
  canNavigatePrev: boolean;
  canNavigateNext: boolean;
  showNavigation: boolean;
}

interface Emits {
  navigatePrev: [];
  navigateNext: [];
}

const props = defineProps<Props>();
const emit = defineEmits<Emits>();

const { locale } = useI18n();
const isRTL = computed(() => locale.value === "he");

// Keyboard navigation handlers
const handleKeydown = (event: KeyboardEvent, action: "prev" | "next") => {
  if (event.key === "Enter" || event.key === " ") {
    event.preventDefault();
    if (action === "prev" && props.canNavigatePrev) {
      emit("navigatePrev");
    }
    else if (action === "next" && props.canNavigateNext) {
      emit("navigateNext");
    }
  }
};
</script>

<template>
  <div class="flex items-center justify-between w-full px-4" role="navigation" aria-label="Day navigation">
    <!-- Left Navigation Arrow -->
    <BaseButton
      v-if="showNavigation"
      name="arrow-left"
      type="icon"
      class="text-main"
      :class="{ 'opacity-50 cursor-not-allowed': !canNavigatePrev }"
      :disabled="!canNavigatePrev"
      :aria-label="isRTL ? 'Next day' : 'Previous day'"
      :aria-disabled="!canNavigatePrev"
      tabindex="0"
      @click="$emit('navigatePrev')"
      @keydown="handleKeydown($event, 'prev')"
    >
      <svg class="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" aria-hidden="true">
        <!-- For RTL (Hebrew), flip the arrow direction -->
        <path
          v-if="isRTL"
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M9 5l7 7-7 7"
        />
        <path
          v-else
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M15 19l-7-7 7-7"
        />
      </svg>
    </BaseButton>

    <!-- UTabs Component -->
    <UTabs
      :tabs="tabs"
      class="w-full"
      :active="activeTab"
      role="tablist"
      aria-label="Day selection tabs"
    />

    <!-- Right Navigation Arrow -->
    <BaseButton
      v-if="showNavigation"
      type="icon"
      class="text-main"
      name="arrow-right"
      :class="{ 'opacity-50 cursor-not-allowed': !canNavigateNext }"
      :disabled="!canNavigateNext"
      :aria-label="isRTL ? 'Previous day' : 'Next day'"
      :aria-disabled="!canNavigateNext"
      tabindex="0"
      @click="$emit('navigateNext')"
      @keydown="handleKeydown($event, 'next')"
    >
      <svg class="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" aria-hidden="true">
        <!-- For RTL (Hebrew), flip the arrow direction -->
        <path
          v-if="isRTL"
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M15 19l-7-7 7-7"
        />
        <path
          v-else
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M9 5l7 7-7 7"
        />
      </svg>
    </BaseButton>
  </div>
</template>
