import type { ChannelProgramInterval } from "./use-channel-program";
import { nextTick } from "vue";

export const useDayNavigation = (allDays: Ref<ChannelProgramInterval[]>) => {
  const currentDayIndex = ref(0);
  const maxVisibleDays = ref(6);
  const activeDayId = ref(1);
  const previousDayId = ref(1);
  const slideDirection = ref<"left" | "right">("right");

  const activeIndex = computed(() => {
    return allDays.value.findIndex(day => day.id === activeDayId.value);
  });

  const visibleDays = computed(() => {
    const startIndex = currentDayIndex.value;
    const endIndex = Math.min(startIndex + maxVisibleDays.value, allDays.value.length);
    return allDays.value.slice(startIndex, endIndex);
  });

  const canNavigatePrev = computed(() => {
    return activeIndex.value > 0;
  });

  const canNavigateNext = computed(() => {
    return activeIndex.value < allDays.value.length - 1;
  });

  const adjustVisibleWindow = () => {
    const currentActiveIndex = activeIndex.value;
    if (currentActiveIndex === -1)
      return;

    const windowStart = currentDayIndex.value;
    const windowEnd = windowStart + maxVisibleDays.value - 1;

    if (currentActiveIndex < windowStart) {
      currentDayIndex.value = currentActiveIndex;
    }
    else if (currentActiveIndex > windowEnd) {
      currentDayIndex.value = Math.max(0, currentActiveIndex - maxVisibleDays.value + 1);
    }
  };

  const navigatePrev = () => {
    if (canNavigatePrev.value) {
      const currentActiveIndex = activeIndex.value;
      const prevDay = allDays.value[currentActiveIndex - 1];

      if (prevDay) {
        slideDirection.value = "left";
        previousDayId.value = activeDayId.value;
        activeDayId.value = prevDay.id;

        nextTick(() => {
          adjustVisibleWindow();
        });
      }
    }
  };

  const navigateNext = () => {
    if (canNavigateNext.value) {
      const currentActiveIndex = activeIndex.value;
      const nextDay = allDays.value[currentActiveIndex + 1];

      if (nextDay) {
        slideDirection.value = "right";
        previousDayId.value = activeDayId.value;
        activeDayId.value = nextDay.id;

        nextTick(() => {
          adjustVisibleWindow();
        });
      }
    }
  };

  const selectDay = (dayId: number) => {
    const currentActiveIndex = activeIndex.value;
    const newDayIndex = allDays.value.findIndex(d => d.id === dayId);

    if (newDayIndex > currentActiveIndex) {
      slideDirection.value = "right";
    }
    else if (newDayIndex < currentActiveIndex) {
      slideDirection.value = "left";
    }

    previousDayId.value = activeDayId.value;
    activeDayId.value = dayId;
  };

  const updateMaxVisibleDays = () => {
    const width = window.innerWidth;

    if (width < 640) {
      maxVisibleDays.value = 2;
    }
    else if (width < 768) {
      maxVisibleDays.value = 3;
    }
    else if (width < 1024) {
      maxVisibleDays.value = 4;
    }
    else {
      maxVisibleDays.value = 5;
    }

    adjustVisibleWindow();
  };

  const showNavigation = computed(() => allDays.value.length > maxVisibleDays.value);

  const currentDayPrograms = computed(() => {
    const currentDay = allDays.value.find(day => day.id === activeDayId.value);
    return currentDay?.programs || [];
  });

  const tabsForUTabs = computed(() => {
    return visibleDays.value.map(day => ({
      key: day.id.toString(),
      label: day.label,
      onClick: () => selectDay(day.id),
    }));
  });

  const setTodayAsActive = () => {
    const now = new Date();
    const todayString = `${now.getFullYear()}-${String(now.getMonth() + 1).padStart(2, "0")}-${String(now.getDate()).padStart(2, "0")}`;

    const todayDay = allDays.value.find(day => day.date === todayString);

    if (todayDay) {
      // Set today as active
      activeDayId.value = todayDay.id;
      previousDayId.value = todayDay.id;

      // Adjust visible window to show today
      nextTick(() => {
        adjustVisibleWindow();
      });
    }
  };

  // Get current day info
  const currentDay = computed(() => {
    return allDays.value.find(day => day.id === activeDayId.value);
  });

  return {
    // State
    activeDayId: readonly(activeDayId),
    previousDayId: readonly(previousDayId),
    slideDirection: readonly(slideDirection),

    // Computed
    visibleDays,
    canNavigatePrev,
    canNavigateNext,
    showNavigation,
    currentDayPrograms,
    tabsForUTabs,
    currentDay,

    // Methods
    navigatePrev,
    navigateNext,
    selectDay,
    updateMaxVisibleDays,
    setTodayAsActive,
  };
};
