import formatDate from "./format-date";

/**
 * Groups an array of linear programs by date, formatting the date and organizing the data accordingly.
 *
 * @param {EpgItem[]} data - Array of linear program objects containing dateTime, title, and type.
 * @param {TAvailableLocale} [locale] - The locale used for date formatting. Defaults to 'en'.
 * @returns {Record<string, EpgItem[]>} An object where keys are formatted dates and values are arrays of linear programs.
 */
export const groupLinearProgramByDate = (data: EpgItem[], locale: TAvailableLocale = "en") => {
  return data.reduce((groupedData: Record<string, EpgItem[]>, item: EpgItem) => {
    const dateTime = new Date(item.dateTime);
    const formattedDate = formatDate(dateTime, "LLLL d, cccc", locale);

    groupedData[formattedDate] = groupedData[formattedDate] || [];
    groupedData[formattedDate].push({
      dateTime: formatDate(dateTime, "p", locale),
      title: item.title,
      type: item.type,
    });

    return groupedData;
  }, {});
};

/**
 * Groups linear programs by day, returning structured data with date info and programs.
 *
 * @param {EpgItem[]} data - Array of linear program objects containing dateTime, title, and type.
 * @param {TAvailableLocale} [locale] - The locale used for date formatting. Defaults to 'en'.
 * @returns {Array} Array of objects with date info and grouped programs.
 */
export const groupLinearProgramByDay = (data: EpgItem[], locale: TAvailableLocale = "en") => {
  const groupedByDate = new Map<string, { date: Date; programs: Array<EpgItem & { originalDateTime: Date }> }>();

  data.forEach((item) => {
    const dateTime = new Date(item.dateTime);

    // Validate the date
    if (Number.isNaN(dateTime.getTime())) {
      console.warn("Invalid date found:", item.dateTime);
      return;
    }

    // Create a date key for grouping (YYYY-MM-DD)
    const dateKey = formatDate(dateTime, "yyyy-MM-dd", locale);

    if (!groupedByDate.has(dateKey)) {
      groupedByDate.set(dateKey, {
        date: dateTime,
        programs: [],
      });
    }

    groupedByDate.get(dateKey)!.programs.push({
      dateTime: formatDate(dateTime, "p", locale), // Format time only
      title: item.title,
      type: item.type,
      originalDateTime: dateTime, // Keep original date for sorting
    });
  });

  // Convert Map to array and sort by date
  return Array.from(groupedByDate.entries())
    .sort(([dateKeyA], [dateKeyB]) => dateKeyA.localeCompare(dateKeyB))
    .map(([dateKey, { date, programs }]) => ({
      dateKey,
      date,
      programs: programs
        .sort((a, b) => a.originalDateTime.getTime() - b.originalDateTime.getTime()) // Sort by time
        .map(({ originalDateTime: _originalDateTime, ...program }) => program), // Remove originalDateTime from final result
    }));
};
