<script setup lang="ts">
import { groupLinearProgramByDate } from "~/utils/linear-program";

interface Props {
  event: IModelEvent;
  programs: EpgItem[] | null;
}

const props = defineProps<Props>();

const { locale } = useI18n();

const formattedPrograms = computed(() => {
  if (!props.programs || !props.programs.length)
    return null;

  return groupLinearProgramByDate(props.programs, locale.value as TAvailableLocale);
});
</script>

<template>
  <div class="flex flex-col lg:mb-10 lg:flex-row gap-6" :class="{ 'justify-center': !formattedPrograms }">
    <!-- Video Player Section -->
    <div class="w-full rounded overflow-hidden" :class="[formattedPrograms ? 'lg:w-2/3' : 'lg:w-4/5 xl:w-2/3']">
      <EventBlockVideo :event="event" />
      <div class="flex rounded-b w-full flex-col background-main py-6 px-4">
        <div class="flex justify-between items-center">
          <div class="text-[var(--base-tab-text-color)] font-medium truncate">
            {{ event?.title }}
          </div>
          <EventInfoShare :event="event" />
        </div>

        <EventInfoDescription v-if="event?.eventDescription" :description="event.eventDescription" />
      </div>
    </div>

    <!-- Channel Program Section -->
    <EventChannelProgram v-if="formattedPrograms" class="lg:w-1/3 aspect-[1]" :linear-program-item="formattedPrograms" />
  </div>
</template>
