/**
 * @fileoverview Enhanced Channel Program Composable
 *
 * This composable provides flexible EPG (Electronic Program Guide) data transformation
 * with support for multiple time intervals (day, week, month, quarter, year).
 *
 * @example Basic usage:
 * ```typescript
 * const { transformChannelProgramToIntervals } = useChannelProgram();
 *
 * // Group by day (default)
 * const dayPrograms = transformChannelProgramToIntervals(epgData, "day");
 *
 * // Group by week
 * const weekPrograms = transformChannelProgramToIntervals(epgData, "week");
 *
 * // Group by month
 * const monthPrograms = transformChannelProgramToIntervals(epgData, "month");
 *
 * ```
 *
 * @example Advanced usage with reactive intervals:
 * ```typescript
 * const { transformChannelProgramToIntervals, IntervalType } = useChannelProgram();
 *
 * const selectedInterval = ref<IntervalType>("week");
 *
 * const programs = computed(() =>
 *   transformChannelProgramToIntervals(epgData.value, selectedInterval.value)
 * );
 *
 * // Change interval dynamically
 * selectedInterval.value = "month";
 * ```
 *
 * @example Return data structure:
 * ```typescript
 * interface ChannelProgramInterval {
 *   id: number;                    // Sequential interval ID
 *   label: string;                 // Short display label (e.g., "Monday 15/01", "Week 3")
 *   fullDate: string;              // Full date description (e.g., "January 15, Monday", "Week 3, 2024")
 *   date: string;                  // ISO date key (yyyy-MM-dd)
 *   startDate: Date;               // Interval start date
 *   endDate: Date;                 // Interval end date
 *   intervalType: IntervalType;    // The interval type used
 *   programs: Array<{
 *     time: string;                // Formatted time (e.g., "2:30 PM")
 *     title: string;               // Program title
 *     description: string;         // Program description
 *     originalDate: Date;          // Original program date/time
 *   }>;
 * }
 * ```
 */

import formatDate from "~/utils/format-date";

// Define supported interval types
export type IntervalType = "day" | "week" | "month";

export interface ChannelProgramInterval {
  id: number;
  label: string;
  fullDate: string;
  date: string;
  startDate: Date;
  endDate: Date;
  intervalType: IntervalType;
  programs: {
    time: string;
    title: string;
    description: string;
    originalDate: Date;
  }[];
}

export const useChannelProgram = () => {
  const { locale } = useI18n();

  /**
   * Generate interval ranges based on the specified interval type
   */
  const generateIntervalRange = (
    startDate: Date,
    endDate: Date,
    intervalType: IntervalType,
  ): { start: Date; end: Date }[] => {
    const intervals: { start: Date; end: Date }[] = [];
    const currentDate = new Date(startDate);

    // Normalize to start of interval
    const normalizedStart = new Date(currentDate);
    switch (intervalType) {
      case "day":
        normalizedStart.setHours(0, 0, 0, 0);
        break;
      case "week": {
        // Start of week (Monday)
        const dayOfWeek = normalizedStart.getDay();
        const diff = dayOfWeek === 0 ? -6 : 1 - dayOfWeek;
        normalizedStart.setDate(normalizedStart.getDate() + diff);
        normalizedStart.setHours(0, 0, 0, 0);
        break;
      }
      case "month":
        normalizedStart.setDate(1);
        normalizedStart.setHours(0, 0, 0, 0);
        break;
    }

    const current = new Date(normalizedStart);
    const endTime = endDate.getTime();

    while (current.getTime() <= endTime) {
      const intervalStart = new Date(current);
      const intervalEnd = new Date(current);

      // Calculate interval end
      switch (intervalType) {
        case "day":
          intervalEnd.setDate(intervalEnd.getDate() + 1);
          break;
        case "week":
          intervalEnd.setDate(intervalEnd.getDate() + 7);
          break;
        case "month":
          intervalEnd.setMonth(intervalEnd.getMonth() + 1);
          break;
      }

      intervalEnd.setTime(intervalEnd.getTime() - 1); // End at last millisecond of interval

      intervals.push({ start: intervalStart, end: intervalEnd });

      // Move to next interval
      current.setTime(intervalEnd.getTime() + 1);
    }

    return intervals;
  };

  /**
   * Group EPG data by specified interval
   */
  const groupEpgByInterval = (
    epgData: EpgItem[],
    intervalType: IntervalType,
  ): Map<string, { start: Date; end: Date; programs: Array<EpgItem & { originalDate: Date }> }> => {
    const grouped = new Map();

    epgData.forEach((item) => {
      const dateTime = new Date(item.dateTime);

      if (Number.isNaN(dateTime.getTime())) {
        console.warn("Invalid date found:", item.dateTime);
        return;
      }

      // Find the interval this date belongs to
      const intervalStart = new Date(dateTime);
      switch (intervalType) {
        case "day":
          intervalStart.setHours(0, 0, 0, 0);
          break;
        case "week": {
          const dayOfWeek = intervalStart.getDay();
          const diff = dayOfWeek === 0 ? -6 : 1 - dayOfWeek;
          intervalStart.setDate(intervalStart.getDate() + diff);
          intervalStart.setHours(0, 0, 0, 0);
          break;
        }
        case "month":
          intervalStart.setDate(1);
          intervalStart.setHours(0, 0, 0, 0);
          break;
      }

      const intervalEnd = new Date(intervalStart);
      switch (intervalType) {
        case "day":
          intervalEnd.setDate(intervalEnd.getDate() + 1);
          break;
        case "week":
          intervalEnd.setDate(intervalEnd.getDate() + 7);
          break;
        case "month":
          intervalEnd.setMonth(intervalEnd.getMonth() + 1);
          break;
      }
      intervalEnd.setTime(intervalEnd.getTime() - 1);

      const intervalKey = formatDate(intervalStart, "yyyy-MM-dd", locale.value as TAvailableLocale);

      if (!grouped.has(intervalKey)) {
        grouped.set(intervalKey, {
          start: intervalStart,
          end: intervalEnd,
          programs: [],
        });
      }

      grouped.get(intervalKey)!.programs.push({
        dateTime: formatDate(dateTime, "p", locale.value as TAvailableLocale),
        title: item.title,
        type: item.type,
        originalDate: dateTime,
      });
    });

    return grouped;
  };

  /**
   * Get appropriate format strings for different intervals
   */
  const getIntervalFormats = (intervalType: IntervalType) => {
    switch (intervalType) {
      case "day":
        return {
          labelFormat: "EE dd/MM",
          fullDateFormat: "LLLL d, cccc",
        };
      case "week":
        return {
          labelFormat: "'Week' w", // Will be overridden in the main function
          fullDateFormat: "'Week' w, yyyy", // Will be overridden in the main function
        };
      case "month":
        return {
          labelFormat: "MMMM yyyy",
          fullDateFormat: "LLLL yyyy",
        };
      default:
        return {
          labelFormat: "EEEE dd/MM",
          fullDateFormat: "LLLL d, cccc",
        };
    }
  };

  /**
   * Create custom week label in format "Sep 12 - Sep 19"
   */
  const createWeekLabel = (startDate: Date, endDate: Date, locale: TAvailableLocale): { short: string; full: string } => {
    try {
      const actualEndDate = new Date(endDate);
      actualEndDate.setDate(actualEndDate.getDate() - 1); // Adjust end date to last day of week

      const startMonth = formatDate(startDate, "MMM", locale);
      const endMonth = formatDate(actualEndDate, "MMM", locale);
      const startDay = formatDate(startDate, "d", locale);
      const endDay = formatDate(actualEndDate, "d", locale);
      const year = formatDate(startDate, "yyyy", locale);

      // If same month: "Sep 12 - 19"
      if (startMonth === endMonth) {
        return {
          short: `${startMonth} ${startDay} - ${endDay}`,
          full: `${startMonth} ${startDay} - ${endDay}, ${year}`,
        };
      }
      // If different months: "Sep 30 - Oct 6"
      else {
        return {
          short: `${startMonth} ${startDay} - ${endMonth} ${endDay}`,
          full: `${startMonth} ${startDay} - ${endMonth} ${endDay}, ${year}`,
        };
      }
    }
    catch (error) {
      console.warn("Error creating week label:", error);
      return {
        short: "Week",
        full: "Week",
      };
    }
  };

  /**
   * Transform EPG data to match UI structure with dynamic intervals
   */
  const transformChannelProgramToIntervals = (
    epgData: EpgItem[],
    intervalType: IntervalType = "day",
  ): ChannelProgramInterval[] => {
    if (!epgData?.length) {
      console.warn("No EPG data provided to transformChannelProgramToIntervals");
      return [];
    }

    try {
      const groupedPrograms = groupEpgByInterval(epgData, intervalType);

      if (!groupedPrograms.size) {
        console.warn("No programs were grouped by interval");
        return [];
      }

      // Find the date range from EPG data
      const dates = epgData
        .map(item => new Date(item.dateTime))
        .filter(date => !Number.isNaN(date.getTime()))
        .sort((a, b) => a.getTime() - b.getTime());

      if (!dates.length) {
        console.warn("No valid dates found in EPG data");
        return [];
      }

      const startDate = dates[0];
      const endDate = dates[dates.length - 1];

      // Generate all intervals in the range
      const intervals = generateIntervalRange(startDate, endDate, intervalType);
      const formats = getIntervalFormats(intervalType);

      return intervals.map((interval, index) => {
        const intervalKey = formatDate(interval.start, "yyyy-MM-dd", locale.value as TAvailableLocale);
        const intervalData = groupedPrograms.get(intervalKey);

        try {
          let shortLabel: string;
          let fullDateLabel: string;

          // Special handling for week intervals
          if (intervalType === "week") {
            const weekLabels = createWeekLabel(interval.start, interval.end, locale.value as TAvailableLocale);
            shortLabel = weekLabels.short;
            fullDateLabel = weekLabels.full;
          }
          else {
            // Use standard format for other intervals
            shortLabel = formatDate(interval.start, formats.labelFormat, locale.value as TAvailableLocale) || `${intervalType} ${index + 1}`;
            fullDateLabel = formatDate(interval.start, formats.fullDateFormat, locale.value as TAvailableLocale) || shortLabel;
          }

          return {
            id: index + 1,
            label: shortLabel,
            fullDate: fullDateLabel,
            date: intervalKey,
            startDate: interval.start,
            endDate: interval.end,
            intervalType,
            programs: intervalData
              ? intervalData.programs
                  .sort((a, b) => a.originalDate.getTime() - b.originalDate.getTime())
                  .map(program => ({
                    time: program.dateTime || "N/A",
                    title: program.title || "Untitled Program",
                    description: program.type || "No description",
                    originalDate: program.originalDate,
                  }))
              : [],
          };
        }
        catch (formatError) {
          console.warn(`Error formatting ${intervalType} ${index + 1}:`, formatError);
          return {
            id: index + 1,
            label: `${intervalType} ${index + 1}`,
            fullDate: `${intervalType} ${index + 1}`,
            date: intervalKey || `fallback-${index}`,
            startDate: interval.start,
            endDate: interval.end,
            intervalType,
            programs: [],
          };
        }
      });
    }
    catch (error) {
      console.error("Error transforming channel program data:", error);
      return [];
    }
  };

  return {
    transformChannelProgramToIntervals,
  };
};
