<script setup lang="ts">
import type { ComponentPublicInstance } from "vue";
import formatDate from "~/utils/format-date";

export interface ChannelProgramItemProps {
  linearProgramItem: Record<string, EpgItem[]>;
}
const props = defineProps<ChannelProgramItemProps>();

const { locale } = useI18n();
const containerRef = ref<HTMLElement>();
const dateRefs = ref<Record<string, HTMLElement>>({});

// Get today's date in the same format as the program keys
const getTodayKey = () => {
  const today = new Date();
  return formatDate(today, "LLLL d, cccc", locale.value as TAvailableLocale);
};

// Scroll to today's date section if it exists
const scrollToToday = () => {
  const todayKey = getTodayKey();
  const todayElement = dateRefs.value[todayKey];

  if (todayElement && containerRef.value) {
    // Calculate the scroll position within the container only
    const containerRect = containerRef.value.getBoundingClientRect();
    const elementRect = todayElement.getBoundingClientRect();
    const scrollTop = elementRect.top - containerRect.top + containerRef.value.scrollTop;

    // Scroll only within the container, not the entire page
    containerRef.value.scrollTo({
      top: scrollTop,
      behavior: "smooth",
    });
  }
};

// Set ref for date sections
const setDateRef = (key: string) => (el: Element | ComponentPublicInstance | null) => {
  if (el && "scrollIntoView" in el) {
    dateRefs.value[key] = el as HTMLElement;
  }
};

// Watch for changes in program data and scroll to today when available
watch(() => props.linearProgramItem, (newPrograms) => {
  if (newPrograms && Object.keys(newPrograms).length > 0) {
    nextTick(() => {
      scrollToToday();
    });
  }
}, { immediate: true });

onMounted(() => {
  // Also try to scroll on mount in case data is already available
  if (Object.keys(props.linearProgramItem).length > 0) {
    nextTick(() => {
      scrollToToday();
    });
  }
});
</script>

<template>
  <div ref="containerRef" class="background-main rounded px-4 pb-4 w-full overflow-auto">
    <div v-for="(data, key) in props.linearProgramItem" :key="key">
      <p
        :ref="setDateRef(key as string)"
        class="text-secondary text-base font-medium mb-4 mt-4 sticky top-0 bg-[var(--base-card-bgColor)] py-2 z-10"
        :class="{ 'text-primary font-bold': key === getTodayKey() }"
      >
        {{ key }}
        <span v-if="key === getTodayKey()" />
      </p>
      <div v-for="(item, index) in data" :key="index">
        <div class="w-full flex min-h-[75px] pb-2">
          <div
            class="flex w-1/4 bg-[var(--base-favorite-club-chip-bgColor)] ltr:rounded-l rtl:rounded-r pt-4 px-4 justify-center text-main font-bold text-lg"
          >
            {{ item.dateTime }}
          </div>
          <div class="flex w-3/4 bg-[var(--base-countdown-bgColor)] ltr:rounded-r rtl:rounded-l text-main flex-col p-4 gap-2">
            <div class="text-sm font-medium">
              {{ item.title }}
            </div>
            <div class="text-secondary text-xs">
              {{ item.type }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
