<!-- eslint-disable max-len -->
<script setup lang="ts">
import { useSwipe } from "@vueuse/core";
import { useChannelProgram } from "~/composables/use-channel-program";
import { useDayNavigation } from "~/composables/use-day-navigation";
import useEventStore from "~/stores/event";

const route = useRoute();
const eventStore = useEventStore();
const event = computed(() => eventStore.data);
const loading = computed(() => eventStore.loading);
const channelProgram = computed(() => eventStore.channelProgram);

const nationalProgram = computed(() => !event.value?.source?.hd);

const title = computed(() => {
  if (nationalProgram.value) {
    return "TV Program";
  }
  return event.value?.title || "Direto";
});

const { transformChannelProgramToIntervals } = useChannelProgram();

// Extract EPG data from the linear program
const epgData = computed(() => {
  const program = channelProgram.value;
  if (!program)
    return [];

  if (nationalProgram.value) {
    return program.epg?.length ? program.epg : program.epgs?.[0]?.items || [];
  }

  // Return epg array or first epgs item's items array
  return program.epg?.length ? program.epg : program.epgs?.[1]?.items || [];
});

const allDays = computed(() => {
  return transformChannelProgramToIntervals(epgData.value, "day");
});

const {
  activeDayId,
  slideDirection,
  canNavigatePrev,
  canNavigateNext,
  showNavigation,
  currentDayPrograms,
  tabsForUTabs,
  currentDay,
  navigatePrev,
  navigateNext,
  updateMaxVisibleDays,
  setTodayAsActive,
} = useDayNavigation(allDays);

const swipeContainer = ref<HTMLElement>();
useSwipe(swipeContainer, {
  threshold: 10,
  onSwipeEnd: (_e: TouchEvent, direction: string) => {
    if (direction.toLowerCase() === "left") {
      navigateNext();
    }
    else if (direction.toLowerCase() === "right") {
      navigatePrev();
    }
  },
});

onMounted(() => {
  updateMaxVisibleDays();
  window.addEventListener("resize", updateMaxVisibleDays);
});

onUnmounted(() => {
  window.removeEventListener("resize", updateMaxVisibleDays);
});

watch(allDays, (newDays) => {
  if (newDays.length > 0) {
    setTodayAsActive();
  }
}, { immediate: true });

watch(() => route.params.eventId, (eventId) => {
  eventStore.reset();
  eventId && eventStore.load(eventId as string);
}, { immediate: true });

useHead({ title: computed(() => title.value) });
definePageMeta({ layout: "home" });
</script>

<template>
  <div class="main-container">
    <div v-if="loading" class="flex flex-col lg:flex-row mb-10 lg:gap-6">
      <div class="skeletonLoading aspect-video lg:w-2/3" />
      <div class="skeletonLoading lg:w-1/3" />
    </div>

    <div v-else-if="nationalProgram" class="max-w-screen-2xl mx-auto">
      <div ref="swipeContainer" class="flex flex-col gap-10">
        <h1 class="text-main text-3xl font-bold">
          {{ title }}
        </h1>

        <div class="background-main min-h-[calc(100vh-350px)] rounded sm:px-6 py-4">
          <ChannelDayNavigation
            :tabs="tabsForUTabs"
            :active-tab="activeDayId.toString()"
            :can-navigate-prev="canNavigatePrev"
            :can-navigate-next="canNavigateNext"
            :show-navigation="showNavigation"
            @navigate-prev="navigatePrev"
            @navigate-next="navigateNext"
          />

          <ChannelProgramList
            :programs="currentDayPrograms"
            :slide-direction="slideDirection"
            :day-key="activeDayId"
            :full-date="currentDay?.fullDate"
          />
        </div>
      </div>
    </div>
    <EventPlayerWithProgram
      v-else-if="event"
      :event="event"
      :programs="epgData"
    />
  </div>
</template>

<style>
.homeLayout__content {
  @apply !min-h-[calc(100vh-500px)] sm:!min-h-[calc(100vh-305px)]
}

.baseFooter {
  @apply min-h-[500px] sm:min-h-[unset]
}
</style>
